$red: #FF707E;
$green: #45CB85;
$blue: #294C60;

$todo-height: 3rem;

* {
    margin: 0;
    box-sizing: border-box;
}

@font-face {
	font-family: 'Plus Jakarta Sans';
	src: url('./fonts/PlusJakartaSans-Bold.woff2') format('woff2'),
		 url('./fonts/PlusJakartaSans-Bold.woff') format('woff');
	font-weight: 700;
	font-style: normal;
	font-display: fallback;
}

@font-face {
	font-family: 'Plus Jakarta Sans';
	src: url('./fonts/PlusJakartaSans-Regular.woff2') format('woff2'),
		 url('./fonts/PlusJakartaSans-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: fallback;
}

	$normal-text-family: 'Plus Jakarta Sans';

li {
    list-style: none;
}

ul {
    padding: 0;
}

body {
    background: $red;
    font-family: $normal-text-family;
    // height: 100vh;
    // width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1 {
    margin-bottom: 4rem;
    margin-top: 5rem;
    color: white;
    font-weight: 900;
}

.completed {
    position: relative;
    
    .todo-item {
        opacity: .5;
    }

    .todo-item {
        position: relative;
        text-decoration: line-through;
        

    }
}

form {
    display: flex;
    flex-direction: column;
    gap: 5rem;
    margin-bottom: 5rem;
    width: 100%;

    @media screen and (min-width: 768px){
        flex-direction: row;
    }

    .form-input {
        display: flex;
    width: 100%;


        .todo-input {
            height: 40px;
            max-width: 14rem;
            padding: 0;
            border-radius: 0;
            border: none;
            padding: .5rem;
            font-size: 1rem;
            border-radius: 6px;
        }

        .todo-button {
            height: 40px;
            display: block;
            border: none;
            border-radius: 6px;
            width: 60px;
            background: transparent;
            font-size: 1.5rem;
            font-weight: 900;
            color: white;
        }
    }


    .form-options {
        display: flex;

        .filter-todo {
            height: 40px;
            font-size: 1rem;
            font-weight: 700;
            color: $blue;
            border: none;
            border-radius: 6px;

            &:active,
            &:focus {
                outline: none;
            }
        }
    }
}

.todo-container {

    .todo-list {
        display: flex;
        flex-direction: column;
        row-gap: 2rem;

        .todo {
            display: flex;
            justify-content: space-between;
            background: white;

            .todo-item {
                width: 100%;
                border-radius: 6px;
                height: $todo-height;
                display: flex;
                align-items: center;
                padding: 0.5rem;
                font-weight: 700;
            }

            .todo-action {
                height: 100%;
                display: flex;

                button {
                    height: $todo-height;
                    width: 40px;
                    display: block;
                    cursor: pointer;
                    border: none;
                }
            }
        }
    }
}